import {
    Form,
    FormGroup,
    FormControl,
    ControlLabel,
    Button,
    ButtonToolbar,
    Panel
} from 'rsuite';
import CardResult from './CardResult';
import React, {useState} from 'react';

function FormSirio({onSelect, activeKey, ...props}) {
    const [card, setCard] = useState();
    const [textArea, setTextArea] = useState("");


    function showCard() {
        if (0 !== textArea.trim().localeCompare("")) {
            setCard(
                <CardResult
                    codigo={textArea}
                />
            )
        } else{
            setCard()
        }
    }

    function clear() {
        setTextArea("")
    }

    return (
        <div><Panel header="Panel title" bordered style={{width: "600px"}}>
            <p/>
            <Form fluid>
                <FormGroup>
                    <ControlLabel>Código Dart</ControlLabel>
                    <FormControl rows={10} name="textarea" componentClass="textarea"
                                 value={textArea} onChange={v => setTextArea(v)}
                    />
                </FormGroup>
                <FormGroup>
                    <ButtonToolbar>
                        <Button appearance="default" color="green" onClick={showCard}>Analizar</Button>
                        <Button appearance="default" onClick={clear}>Limpiar</Button>
                    </ButtonToolbar>
                </FormGroup>
            </Form>
        </Panel>
            {card}
        </div>
    );
}

export default FormSirio;
