import {
    Panel,
    Divider,
    Row
} from 'rsuite';
import consNum from "../Automaton/consNum";

function CardResult({codigo, onSelect, activeKey, ...props }) {
    let esNumero = consNum(codigo+"")
    return (
        <Row>
            <Divider> <h3> Lista ligada </h3></Divider>
            <Panel bordered >
                {esNumero}
            </Panel>
        </Row>

    );

}

export default CardResult;
