import {
    Header,
    Navbar
} from 'rsuite';

function header({onSelect, activeKey, ...props}) {

    const logoStyle = {
        padding: "18px 20px", display: "inline-block"
    };
    
    return (
        <Header>
                <Navbar {...props}>
                    <Navbar.Header>
                        <div style={logoStyle}>
                            SIRIO
                        </div>
                    </Navbar.Header>
                    <Navbar.Body>
                    </Navbar.Body>
                </Navbar>
            </Header>
    );
}

export default header;



